import React, { useContext, useState, useEffect } from "react";

export const ProductsContext = React.createContext({
  products: [],
  toggleFav: (id) => {},
});

export default (props) => {
  const [productList, setProductList] = useState([
    {
      id: "p1",
      title: "Red Scarf",
      description: "A pretty red scarf.",
      isFavorite: false,
    },
    {
      id: "p2",
      title: "Blue T-Shirt",
      description: "A pretty blue t-shirt.",
      isFavorite: false,
    },
    {
      id: "p3",
      title: "Green Trousers",
      description: "A pair of lightly green trousers.",
      isFavorite: false,
    },
    {
      id: "p4",
      title: "Orange Hat",
      description: "Street style! An orange hat.",
      isFavorite: false,
    },
  ]);

  const productFav = (id) => {
    const productToBeChanged = productList.find((item) => item.id === id);

    const productIndex = productList.findIndex((item) => item.id === id);
    const productChanged = {
      ...productToBeChanged,
      isFavorite: !productToBeChanged.isFavorite,
    };

    const updatedProducts = [...productList];

    updatedProducts[productIndex] = productChanged;

    setProductList(updatedProducts);
  };

  return (
    <ProductsContext.Provider
      value={{
        products: productList,
        toggleFav: productFav,
      }}
    >
      {props.children}
    </ProductsContext.Provider>
  );
};
